/* eslint-disable jsx-a11y/alt-text */
/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useState } from 'react'
import Calendar from 'react-calendar'
import 'react-calendar/dist/Calendar.css';
import '../MainContent/MainContent.css'
import ContentTab from '../ContentTab/ContentTab.jsx'
import News from '../MainContent/News'

import Pagin from '../Pagination/Pagin';
export default function MainContent() {
    const [dateState, setDateState] = useState(new Date())
    const changeDate = (e) => {
        setDateState(e)
    }
    return (
        <div className="container">
            <div class="row g-1">
                <div class="col-md-8">
                    <div className='container'>

                        <div className="search pt-5">
                            <nav aria-label="breadcrumb">
                                <ol class="breadcrumb">
                                    <li class="breadcrumb-item"><a href="#"></a></li>
                                    <li class="breadcrumb-item"><a href="#"></a></li>
                                    <li class="breadcrumb-item "><a href="#">English</a></li>
                                </ol>
                            </nav>
                            <div class="input-group rounded">
                                <input type="search" class="form-control rounded" placeholder="" aria-label="Search" aria-describedby="search-addon" />
                                <span class="input-group-text border-0" id="search-addon">
                                    <i class="fas fa-search"></i>
                                </span>
                            </div>
                        </div>
                        <div className='mt-5 pb-5'>
                            <h3></h3>
                            <hr />
                        </div>
                        <News />
                        <Pagin />
                        <ContentTab />
                    </div>


                </div>

                <div class="col-md-3 sidebar-left">
                    <div class="position-sticky "  >
                        <div class="card" >
                            <div class="bg-image hover-overlay ripple" data-mdb-ripple-color="light">
                                <img src="https://www.maorif.tj/images/pressident.png" class="img-fluid" />
                                <a href="#!">
                                </a>
                            </div>
                            <div class="card-body">
                                <p class="card-text text-center">   ,  ,      </p>
                            </div>
                        </div>

                        <div class="p-1">
                            <div class="card">
                                <img src="https://www.maorif.tj/storage/image/td_tj.jpeg" class="card-img-top" alt="..." />

                            </div>
                        </div>

                        <div class="p-1">
                            <div class="card">
                                <img src="https://www.maorif.tj/storage/image/me_tj.jpeg" class="card-img-top" alt="..." />

                            </div>
                        </div>
                        <div class="p-1">
                            <div class="card">
                                <img src="https://www.maorif.tj/storage/image/covid_tj.jpeg" class="card-img-top" alt="..." />

                            </div>
                        </div>
                        <div class="p-1">
                            <div class="card">
                                <p class="card-text text-center pt-4"><b></b> </p>
                                <div>
                                    <Calendar
                                        value={dateState}
                                        onChange={changeDate}
                                    />
                                    {/* <img src="https://calendarbox.ru/wp-content/uploads/2021/03/iun-2022.gif" class="card-img-top" alt="..." /> */}
                                </div>
                            </div>
                        </div>

                        <div class="p-1">
                            <div class="card">
                                <p class="card-text text-center pt-4"> <b> </b> </p>
                                <div>
                                    <a href="https://www.gismeteo.ru/weather-dushanbe-5358/">
                                        <img src="https://klike.net/uploads/posts/2019-02/1550818654_5.jpg" class="card-img-top" alt="..." />
                                    </a>
                                </div>
                            </div>
                        </div>

                        <div class="p-1">
                            <div class="card">
                                <p class="card-text text-center pt-4"><b></b> </p>
                                <div>
                                    <img src="https://s01.flagcounter.com/count2/6Kyg/bg_FFFFFF/txt_000000/border_FFFFFF/columns_3/maxflags_21/viewers_3/labels_0/pageviews_0/flags_0/" class="card-img-top" alt="..." />
                                </div>
                            </div>
                        </div>

                    </div>
                </div>
            </div>
        </div>
    )
}

